@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:900&display=swap);
#background-text-event {
    position: absolute;
    font-size: 18.2vw;
    color: #003CB9;
    bottom: 0vh;
    left: 2%;
    margin-bottom: -2%;
    overflow: hidden;
}
.navigation-bar__container {
    position: absolute;
    display: flex;
    width: 100%;
    right: 1%;
    top: 1vh;
    justify-content: flex-end;
}

.navigation-bar__link-container {
    margin-left: 2%;
    margin-right: 2%;
    display: flex;
}

.navigation-bar__link-container--hover {
    margin-left: 2%;
    margin-right: 2%;
    display: flex;
}

.navigation-bar__link-text {
    -webkit-transition: all 0.15s ease-out 0.15s;
    transition: all 0.15s ease-out 0.15s;
    font-size: 2vw;
    margin-top: 0.5vw;
}

.navigation-bar__link-text--hover {
    -webkit-transition: all 0.15s ease-out 0.15s;
    transition: all 0.15s ease-out 0.15s;
    font-size: 2.5vw;
    margin-top: 0.0vw;
}

.navigation-bar__link-square {
    -webkit-transition: all 0.1s ease-out 0.1s;
    transition: all 0.1s ease-out 0.1s;
    width: 1vw;
    height: 1vw;
    margin-right: 0.25vw;
}

.navigation-bar__link-square--hover {
    -webkit-transition: all 0.15s ease-out 0.15s;
    transition: all 0.15s ease-out 0.15s;
    width: 1.5vw;
    height: 100%;
    margin-right: 0.5vw;
}
.info-contacts {
    display: flex;
    justify-content: space-between;
    margin-left:  7vw;
    margin-right: 14vw;
    position: relative;
    height: 5vh;
}

.info-slogan--container {
    position: absolute;
    top: 35%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.info-slogan--text {
    font-size: 7vw;
}

.info-slogan--title {
    font-size: 10.5vw;
}

.info-slogan--text__adjustement {
    margin-top: -1vh;
}

.info-contacts-title--container {
    margin-left:  5vw;
    justify-content: left;
    display: flex;
    margin-bottom: 1vh;
}

.info-contacts-title--square {
    width: 1.5vw;
    height: 1.5vw;
}

.info-contacts-title--text {
    margin-left:  0.25vw;
    font-size: 4.5vw;
    margin-top: 0.5vh;
}

.info-role {
    -webkit-transition: all 0.1s ease-out 0.1s;
    transition: all 0.1s ease-out 0.1s;
    font-size: 1.75vw;
}

.info-mail {
    -webkit-transition: all 0.1s ease-out 0.1s;
    transition: all 0.1s ease-out 0.1s;
    font-size: 1vw;
    font-weight: 700;
    margin-top: 0vw;
}

.info-role--hover {
    -webkit-transition: all 0.1s ease-out 0.1s;
    transition: all 0.1s ease-out 0.1s;
    font-size: 1vw;
    font-weight: 700;
}

.info-mail--hover {
    -webkit-transition: all 0.1s ease-out 0.1s;
    transition: all 0.1s ease-out 0.1s;
    font-size: 1.75vw;
    margin-top: 0vw;
}

.info-footer {
    position: absolute;
    bottom: 5vh;
    width: 100vw;
}
.home-button-container {
    position: absolute;
    left: 70vw;
    top: 35vh;
}

.nav-button-home-container {
    position: relative;
    margin-bottom: 17vh;
}

.nav-button-home__marging {
    -webkit-transition: all 0.1s ease-out 0.1s;
    transition: all 0.1s ease-out 0.1s;
    top: 0.5vw;
    left: 0.5vw;
    position: absolute;
    display: flex;
}

.nav-button-home__marging--hover {
    -webkit-transition: all 0.1s ease-out 0.1s;
    transition: all 0.1s ease-out 0.1s;
    top: 0vw;
    left: 0vw;
    position: absolute;
    display: flex;
}

.nav-button-home--square {
    width: 1vw;
    height: 1vw;
    margin-right: 0.7vw;
}

.nav-button-home--text {
    font-size: 5vw;
    margin-top: 1vh;
}

.home-logo-element {
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-transition: all 0.1s ease-out 0.1s;
    transition: all 0.1s ease-out 0.1s;
    position: absolute;
    width: 45vw;
    top: 18.5vh;
}

