}

* {
    margin: 0px;
    padding: 0px;
}

.background {
    width: 100%;
    height: 100vh;
    position: relative;
}

.square-title {
    width: 2.25vw;
    height: 2.25vw;
    position: absolute;
    top: 0%;
    left: 0vh;
}

.page-title {
    position: absolute;
    font-size: 6vw;
    left: 3vw;
    top: 1vw;
}

.blue-color--back {
    background-color: #005FFF;
}

.white-color--back {
    background-color: #ffffff;
}

.black-color--back {
    background-color: #000000;
}

.blue-color {
    color: #005FFF;
}

.white-color {
    color: #ffffff;
}

.black-color {
    color: #000000;
}

.font-first {
    font-family: 'Roboto', sans-serif;
    font-weight: 900;
}

.font-second {
    font-family: 'Roboto Slab', sans-serif;
    font-weight: 700;
}

.select-none {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
